<template>
  <main class="main">
    <TransitionGroup name="list">
      <div class="main__chatitem" v-for="(mess, index) in message" :key="index">
        <div
          class="main__chatitem-body"
          :class="{
            send: mess.sendId == sendId,
            recieve: mess.sendId != sendId,
            photo: mess.photo?.length > 0 && mess.body.length == 0,
            'photo-text': mess.photo?.length > 0 && mess.body.length > 0,
          }"
        >
          <span class="main__chatitem-time">{{ mess.time }}</span>
          <div class="main__chatitem-text">
            <img :src="mess.photo" v-if="mess.photo?.length > 0" alt="photo" />
            <p>{{ mess.body }}</p>
          </div>
        </div>
      </div>
    </TransitionGroup>
  </main>
</template>

<script>
export default {
  props: {
    message: Array,
    sendId: Number,
  },
};
</script>

<style>
.main {
  overflow: auto;
  height: 632px;
}
.main::-webkit-scrollbar {
  width: 10px;
}

.main::-webkit-scrollbar-track {
  background: #f1f1f1;
}

.main::-webkit-scrollbar-thumb {
  background: #888;
}

.main::-webkit-scrollbar-thumb:hover {
  background: #555;
}
.main__chatitem {
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.main__chatitem-body {
  display: flex;
  align-items: center;
  gap: 5px;
  padding: 10px;
}
.main__chatitem-text {
  max-width: 80%;
  word-wrap: break-word;
  padding: 5px 10px;
}
.main__chatitem-text img {
  width: 100%;
  border-radius: 13px;
}
.main__chatitem-body.send {
  justify-content: end;
}
.main__chatitem-body.send .main__chatitem-text {
  background: #d0dcff;
  border-radius: 15px 15px 0px 15px;
}
.main__chatitem-body.recieve {
  flex-direction: row-reverse;
  justify-content: flex-end;
}
.main__chatitem-body.recieve .main__chatitem-text {
  background: #c4bfff;
  border-radius: 15px 15px 15px 0px;
}
.list-enter-active,
.list-leave-active {
  transition: 300ms linear;
}
.list-enter-from,
.list-leave-to {
  opacity: 0;
  transform: scale(0.9);
}
.main__chatitem-body.photo .main__chatitem-text {
  border-radius: 15px;
  padding: 2px ;
}
.main__chatitem-body.photo-text .main__chatitem-text {
  border-radius: 15px;
  padding: 2px 2px 6px 2px ;
}
.main__chatitem-body.photo-text p{
  padding: 5px 5px ;
}
</style>