<template>
  <footer class="footer">
    <textarea
      class="footer__edit"
      rows="1"
      placeholder="Написать сообщение..."
      v-model="message"
      @keydown.enter="sendMess"
    ></textarea>
    <button
      class="footer__btn"
      v-if="message.length == 0"
      @click="window = true"
    >
      <img src="@/assets/images/photo.svg" alt="photo" />
    </button>
    <button class="footer__btn" v-else @click="sendMess">
      <img src="@/assets/images/send.svg" alt="send" />
    </button>
    <div class="window" v-if="window" @click="window = false">
      <div class="window__body" @click.stop.prevent>
        <h2 class="window__title">Отправить картинку</h2>
        <div class="window__form">
          <label>
            <span>URL</span>
            <input type="text" v-model="photo" />
          </label>
          <label>
            <span>Комментарий</span>
            <textarea v-model="comment"></textarea>
          </label>
        </div>
        <div class="window__footer">
          <button class="btn red" @click="window = false">Отмена</button>
          <button class="btn blue" @click="sendMessPhoto">ОТПРАВИТЬ</button>
        </div>
      </div>
    </div>
  </footer>
</template>

<script>
export default {
  props: {
    sendId: Number,
  },
  data() {
    return {
      message: "",
      photo: "",
      comment: "",
      window: false,
    };
  },
  methods: {
    sendMess() {
      const time = `${new Date().getHours()}:${new Date().getMinutes()}`;
      if (this.message.length > 0) {
        const mess = {};
        mess.body = this.message.trim();
        mess.sendId = this.sendId;
        mess.time = time;
        this.$emit("new-mess", mess);
        this.message = "";
      }
    },
    sendMessPhoto() {
      const time = `${new Date().getHours()}:${new Date().getMinutes()}`;
      if (this.photo.length > 0) {
        const mess = {};
        mess.body = this.comment.trim();
        mess.photo = this.photo.trim();
        mess.sendId = this.sendId;
        mess.time = time;
        this.$emit("new-mess", mess);
        this.photo = this.comment = "";
        this.window = false;
      }
    },
  },
};
</script>

<style>
.footer {
  padding: 16px 20px;
  width: 100%;
  background: #323232;
  display: flex;
  align-items: center;
  gap: 5px;
}
.footer__edit {
  flex-grow: 1;
  outline: none;
  background: transparent;
  border: none;
  resize: none;
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  color: #ffffff;
}
.footer__edit::placeholder {
  color: #ffffff;
}
.footer__btn {
  width: 24px;
  height: 24px;
}
.window {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(0, 0, 0, 0.35);
}
.window__body {
  background: #f2f7ff;
  border-radius: 28px;
  padding: 24px;
  width: 312px;
}
.window__title {
  font-weight: 400;
  font-size: 24px;
  line-height: 32px;
  margin-bottom: 16px;
  color: #1c1b1f;
}
.window__form {
  display: flex;
  flex-direction: column;
  gap: 16px;
  margin-bottom: 24px;
}
.window__form label {
  position: relative;
}
.window__form span {
  font-weight: 400;
  font-size: 12px;
  line-height: 16px;
  letter-spacing: 0.4px;
  position: absolute;
  top: 8px;
  left: 16px;
  color: #000000;
}
.window__form input,
.window__form textarea {
  border: none;
  outline: none;
  resize: none;
  background: #e2edff;
  border-radius: 4px 4px 0px 0px;
  border-bottom: 1px solid #1c1b1f;
  width: 100%;
  height: 56px;
  padding: 23px 16px 9px;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  display: flex;
  align-items: center;
  letter-spacing: 0.5px;
  color: #49454f;
}
.window__form textarea::-webkit-scrollbar {
  display: none;
}
.window__footer {
  display: flex;
  gap: 8px;
  justify-content: end;
}
.btn {
  padding: 10px 12px;
  border-radius: 5px;
  font-weight: 500;
  font-size: 14px;
  line-height: 20px;
  letter-spacing: 0.1px;
  text-transform: uppercase;
  transition: 0.4s;
}
.btn.red {
  color: #cf1b1b;
}
.btn.red:hover {
  background: rgba(207, 27, 27, 0.151);
}
.btn.blue {
  color: #6750a4;
}
.btn.blue:hover {
  background: rgba(102, 80, 164, 0.13);
}
</style>